	module.exports = {"data":{"markdownRemark":{"htmlAst":{"type":"element","tagName":"div","properties":{},"children":[{"type":"element","tagName":"div","properties":{},"children":[{"type":"text","value":"\nI have found that when I talk with people about engineering methodology, approach, and organization,\none of three things tend to happen:"},{"type":"element","tagName":"ul","properties":{},"children":[{"type":"text","value":"\n  "},{"type":"element","tagName":"li","properties":{},"children":[{"type":"text","value":"They get sleepy"}]},{"type":"text","value":"\n  "},{"type":"element","tagName":"li","properties":{},"children":[{"type":"text","value":"They get get anxious"}]},{"type":"text","value":"\n  "},{"type":"element","tagName":"li","properties":{},"children":[{"type":"text","value":"They seem unsure"}]},{"type":"text","value":"\n"}]},{"type":"element","tagName":"p","properties":{},"children":[{"type":"text","value":"While I'm certain I can express myself in a more clear, concise, and fun way, I have come to a conclusion that\nmost of the many different paths that people take to white-collar industries like technology don't necessarily encourage\nthinking about how to collaborate or strategize to accomplish a goal or complete a project outside of superficial and abstract introductions to \"Agile Methodlogy\" or \"Problem Solving\"."}]},{"type":"element","tagName":"div","properties":{},"children":[{"type":"text","value":"In industry, pretty much every problem, project, and endeavor you encounter will be inherently interdisciplinary, and\nmany, if not all aspects of what it is that you are working on will require multiple disciplines and skill sets. Interdisciplinary endeavors will thus, naturally require collaboration and possibly benefit from some kind of agreed upon roadmap or set of rules to facilitate information-sharing and the implementing of a solution. Thus, any problem-solving model or organizational structure that restricts collaboration or "},{"type":"element","tagName":"a","properties":{"href":"https://www.investopedia.com/terms/s/silo-mentality.asp"},"children":[{"type":"text","value":"'silos'"}]},{"type":"text","value":" different teams that own different responsibilities to achieve a goal will be more likely to operate less efficiently."}]},{"type":"element","tagName":"p","properties":{},"children":[{"type":"text","value":"Let's say you are a UX designer on a team in which all information and deliverables related to a project come from a manager, and that manager is your sole point-of-contact in the project."}]}]}]},"frontmatter":{"title":"Who? What? When? Where? Why? How?"}}},"pathContext":{"slug":"/engineering-who-what-when-where-why-how/"}}

